.root {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 2em;
}

.item-name-input {
  width: 150px !important;
}

.amount-input {
  width: 100px !important;
}

.ratio-input {
  width: 50px !important;
}

.new-person-input {
  width: 140px !important;
}

.add-item-row {
  text-align: center;
}

.add-item-row > * {
  margin: 6px;
}

.summary-row > td > * {
  text-align: center;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
